$background1: #414141;
$background2: #262626;
$background3: #24364d;
$border1: #000;
$font-color: #fff;
$close1: #fff;
$close2: #ff0000;


div#settings {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0px;
  left: 0px;
  z-index: 999;
  background-color: $background1;

  ul.settingsNav {
    border-right: 1px solid $border1;
    background-color: $background2;
    display: inline-block;
    margin: 0px;
    width: 300px;
    height: 100%;
    overflow: auto;
    padding: 0px;

    li {
      list-style: none;
      margin: 0px;
      padding: 20px;
      transition: 100ms;
      cursor: pointer;

      &.active {
        background-color: $background1; }

      &:hover {
        background-color: $background3; }
      & + li {
        border-top: 1px solid $border1; }

      p {
        margin: 0px;
        color: $font-color; } } }

  div.settingsContent {
    color: $background1;
    z-index: 999;
    position: fixed;
    top: 0px;
    left: 301px;
    width: calc(100% - 301px);
    height: 100%;
    margin: 0px;
    padding: 0px;
    color: $font-color;

    .settingsContent-empty {
      width: 100%;
      height: 100%;
      display: inline-block;

      svg {
        width: auto;
        height: 100%;
        position: relative;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        display: inline-block; } } }

  i.settingsClose {
    font-size: 24px;
    z-index: 999;
    position: fixed;
    right: 0px;
    top: 0px;
    color: $close1;

    &:hover {
      color: $close2; } } }

#settings_plugins {
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
  overflow: hidden;

  ul.settings_plugins.menu {
    border-right: 1px solid $border1;
    display: inline-block;
    margin: 0px;
    width: 100%;
    height: 100%;
    overflow: auto;
    padding: 0px;

    li {
      list-style: none;
      margin: 0px;
      padding: 20px;
      transition: 100ms;
      cursor: pointer;

      &.active {
        background-color: $background1; }

      &:hover {
        background-color: $background3; }
      & + li {
        border-top: 1px solid $border1; }

      p {
        margin: 0px;
        color: $font-color; } } }

  .settings_plugins.content {
    color: $font-color;
    padding: 10px;

    div.infos {
      border: 1px solid $border1;

      table {
        width: 100%;
        padding: 0px;

        tr {
          width: 100%;
          margin: 0px;
          padding: 0px;

          th {
            color: $font-color;
            margin: 0px;
            padding: 0px;
            width: 25%;
            text-align: left;
            overflow: hidden;
            display: inline-block;

            & + th {
              width: 75%;
              text-align: right; } } } } }

    div.config {
      border: 0px;
      margin: 0px;
      padding: 0px;

      & .breakpoints {
        width: .8em; }

      & .breakpoint {
        color: #822; }

      & .CodeMirror {
        width: 100%;
        height: 400px; } } } }
